/* divider */
hr {
  border-top-color: #eeeeee;
  opacity: 0.10; }

/* form-control */
.form-control {
  border: 1px solid #eeeeee;
  color: #626262;
  border-radius: 0px;
  height: 50px;
  padding: 10px 20px;
  box-shadow: none;
  font-size: 14px;
  font-weight: 600; }
  .form-control:focus {
    box-shadow: none;
    border-color: #21c87a; }

textarea.form-control {
  height: auto; }

/* custom-file */
.custom-file .custom-file-label {
  height: 50px;
  font-size: 14px;
  font-weight: 600;
  color: #212529;
  border-color: #eeeeee;
  padding: 15px 90px 15px 20px;
  border-radius: 0px; }
  .custom-file .custom-file-label:after {
    height: 48px;
    padding: 14px 25px;
    border-radius: 0px; }

.custom-file .custom-file-input {
  height: 50px;
  color: #212529;
  border-radius: 3px;
  border-color: #eeeeee; }
  .custom-file .custom-file-input:focus ~ .custom-file-label {
    border-color: #21c87a;
    box-shadow: none; }

/* input-group-prepend */
.input-group-prepend .input-group-text,
.input-group-append .input-group-text {
  width: 50px;
  background: transparent;
  border-color: #eeeeee;
  box-shadow: none;
  justify-content: center; }

.input-group-text {
  border-radius: 0px; }

/* checkbox */
.custom-checkbox:checked ~ .custom-control-label:before {
  background: #21c87a;
  border-color: #21c87a; }

.custom-checkbox:not(:disabled):active ~ .custom-control-label:before {
  background: transparent;
  border-color: transparent; }

.custom-checkbox:focus ~ .custom-control-label:before {
  box-shadow: none; }

.custom-checkbox:focus:not(:checked) ~ .custom-control-label:before {
  border-color: #eeeeee; }

.form-check-input[type=checkbox] {
  border-radius: inherit;
  margin-top: 2px; }

.form-check-input {
  border: 2px solid #dfdfdf;
  width: 15px;
  height: 15px;
  margin-top: 0;
  flex: 0 0 15px; }
  .form-check-input:checked {
    background-color: #21c87a;
    border-color: #21c87a;
    border: 2px solid #21c87a; }
  .form-check-input:focus {
    box-shadow: inherit;
    border-color: #21c87a; }

.custom-control-label:before {
  top: 2px; }

.custom-control-label:after {
  top: 2px; }

.custom-checkbox .custom-control-label:before {
  border-radius: 0px;
  border: 2px solid #dfdfdf; }

.form-check label {
  padding-left: 5px; }

.form-check.custom-radio .form-check-input {
  margin-top: 3px; }

/* input-group */
.choose-file.input-group .form-control {
  position: relative;
  z-index: 2;
  width: 100%;
  height: 35px;
  margin: 0;
  opacity: 0;
  color: #001935;
  border-color: #eeeeee; }

.choose-file .input-group-text {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1;
  line-height: 1.5;
  background-color: #ffffff;
  border: 1px solid #ced4da;
  height: 50px;
  font-size: 14px;
  font-weight: 600;
  color: #001935;
  border-color: #eeeeee;
  padding: 15px 90px 15px 20px; }

.choose-file .input-group-text::after {
